.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: aliceblue;
    padding: 50px;
    z-index: 1000;
}

.overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, .7);
    z-index: 1000;
}

.buttons {
    display: flex;
    justify-content: center;
    padding-top: 10px;
}

button {
    margin-left: 5px;
    margin-right: 5px;
}